.gallery {
  width: 320px;
  height: 136px;
  position: relative;
  margin: auto;
  cursor: pointer;

  &__image:hover,
  &__image:focus {
    transform: scale(1.5);
    z-index: 1;
    transition-duration: 1000ms;
  }

  @include tablet {
    width: 768px;
    height: 327px;
  }
  @include desktop {
    width: 1013px;
    height: 430px;
  }
}

.container-gallery {
  margin: auto;
  background: linear-gradient(to bottom, #fff5f6 50%, #fff 50%);
  //   @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  //     background: linear-gradient(var(--secondary-bg-color) 10%, var(--third-text-color) 90%);

  @include tablet {
    background: linear-gradient(to bottom, #fff5f6 38%, #fff 38%);
    // @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    //   background: linear-gradient(var(--secondary-bg-color) 95%, var(--third-text-color) 5%);
  }

  @include desktop {
    background: linear-gradient(to bottom, #fff5f6 38%, #fff 38%);
    //   @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    //     background: linear-gradient(var(--secondary-bg-color) 5%, var(--third-text-color) 95%);
  }
}

